@keyframes animated-gradient {
  0% {
    background-position: 0% 50%;
  }
  33% {
    background-position: 50% 50%;
  }
  66% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.gradient-animation {
  background-image: linear-gradient(120deg, #d184d3, #f0a4c9, #e3d473, #62d797, #7bbcf8, #d184d3);
  text-shadow: 10px 10px 4px rgba(0, 0, 0, 0.1);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  animation: animated-gradient 9s ease infinite;
  background-size: 200% 200%;
  will-change: background-position;
}

html[data-theme='dark'] .gradient-animation {
  background-image: linear-gradient(120deg, #db91fe, #f68fba, #f5e278, #75ffa8, #72efff, #db91fe); /* #db91fe, #72efff, #a4ffb8, #1fc8de, #db91fe */
  text-shadow: none;
}

.pipeline{
  font-size: "inherit";
  font-weight: "inherit";
  line-height: "inherit";
}

 @keyframes move-out {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

/* By default, no animation is applied.
   When the parent (with .group) is hovered, elements with .animate-on-hover get the animation. */
.group:hover .animate-on-hover {
  animation: move-out 1.2s ease infinite;
}

/* Optional: adjust individual delays if desired */
.group:hover .animate-on-hover:nth-child(1) {
  animation-delay: 0s;
}
.group:hover .animate-on-hover:nth-child(2) {
  animation-delay: 0.1s;
}
.group:hover .animate-on-hover:nth-child(3) {
  animation-delay: 0.2s;
}
.group:hover .animate-on-hover:nth-child(4) {
  animation-delay: 0.3s;
}
.group:hover .animate-on-hover:nth-child(5) {
  animation-delay: 0.4s;
}
.group:hover .animate-on-hover:nth-child(6) {
  animation-delay: 0.5s;
}
.group:hover .animate-on-hover:nth-child(7) {
  animation-delay: 0.6s;
}
.group:hover .animate-on-hover:nth-child(8) {
  animation-delay: 0.7s;
}
.group:hover .animate-on-hover:nth-child(9) {
  animation-delay: 0.8s;
}
.group:hover .animate-on-hover:nth-child(10) {
  animation-delay: 0.9s;
}
.group:hover .animate-on-hover:nth-child(11) {
  animation-delay: 1.0s;
}
.group:hover .animate-on-hover:nth-child(12) {
  animation-delay: 1.1s;
}
/* Adjust the arrow delay if necessary (e.g., 13th child) */
.group:hover .animate-on-hover:nth-child(13) {
  animation-delay: 1.2s;
}

:root {
  --inset: 1vmin;
  --color0: #0ac;
  --color1: #222;
  --duration: 5s;
  --bgcolor0: #0aca;
  --bgcolor1: #222a;
}

.avatar-container {
  /* Set the container size as needed */
  width: 60vmin;
  height: 60vmin;
  display: grid;
  place-items: center;
  transform: rotate(45deg);
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 0 10px black, 0 0 10px black;
  filter: drop-shadow(0 0 10px var(--color0)) saturate(2);
}

/* Inner container that clips and rotates the image back */
.avatar-inner {
  width: 100%;
  height: 100%;
  overflow: hidden;
  clip-path: inset(var(--inset));
  display: block;
  position: relative;
}

/* The image fills the inner container */
.avatar-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: rotate(-45deg) scale(1.6);
}

/* The gradient overlay */
.avatar-container::before {
  content: '';
  position: absolute;
  inset: 0;
  background: repeating-conic-gradient(
    transparent,
    var(--color0) 25deg,
    var(--color1) 22.5deg,
    transparent 90deg
  );
  animation: spin var(--duration) linear infinite;
  transform: scale(1.6);
  pointer-events: none;
  z-index: 0;
}

@keyframes spin {
  0% {
    transform: rotate(0) scale(1.6);
  }
  100% {
    transform: rotate(360deg) scale(1.6);
  }
}

@property --angle {
	syntax: '<angle>';
	inherits: true;
	initial-value: 0deg;
}

.view-projects-button {
	--angle: 0deg;
	background: linear-gradient(#ffedd5, #ffedd5) padding-box, 
  conic-gradient(from var(--angle), #dfdfdf, #ffedd5, #92400e) border-box;
	border: 2px solid transparent;
	animation: rotate-gradient 3s infinite linear;
}

.dark .view-projects-button {
	background: linear-gradient(#c2410c, #c2410c) padding-box, 
  conic-gradient(from var(--angle), #232528, #c2410c, #fef3c7) border-box;
}

.view-resume-button {
	--angle: 0deg;
	background: linear-gradient(#cffafe, #cffafe) padding-box, 
  conic-gradient(from var(--angle), #dfdfdf, #cffafe, #1e40af) border-box;
	border: 2px solid transparent;
	animation: rotate-gradient 3s infinite linear;
}

.dark .view-resume-button {
	background: linear-gradient(#155e75, #155e75) padding-box, 
  conic-gradient(from var(--angle), #232528, #155e75, #dbeafe) border-box;
}

@keyframes rotate-gradient {
	from { --angle: 0deg; }
	to { --angle: 360deg; }
}